  padding-top: 20px;
}
.prediv {
    font: 16px/1.5 arial,'Hiragino Sans GB','\5b8b\4f53',sans-serif;
    color: #264663;
    border: 1px solid #7AB1DE;
    border-radius: 2px;
    background: #D2E7F7;
    /*font-size: 12px;*/
    line-height: 22px;
    padding: 5px 10px;
    margin-bottom: 20px;
}
.textDiv {
  display: flex;
  align-items: center;
  margin-bottom: 5px;
  font: 20px/1.5 arial,'Hiragino Sans GB','\5b8b\4f53',sans-serif;
}
.textDiv span {
  width: 5px;
  height: 5px;
  border-radius: 50%;
  background-color: black;
  margin-right: 10px;
}
.textDiv a {
  font-size: 80%;
  color: #909399;
  margin-left: 10px;
  align-self: flex-end;
}
pre {
  background-color: rgb(247,247,247);
  font-size: 16px;
}
</style>



